<template>
    <div class="page">
        <breadCrumb :data="navData"></breadCrumb>
        <div class="containner">
            <div class="session">
                <div class="navTitle">
                    <span class="title">基本信息</span>
                    <i @click="editMessage=true" class="el-icon-edit-outline"></i>
                </div>
                <div class="content">
                    <div class="item">
                        <img src="@/assets/image/logo/logo80.png" alt="">
                        <span class="description" v-if="editMessage==false">
                            <div>实验名称：{{detail.experimentName}}</div>
                            <div>实验状态：{{detail.experimentStatus}}</div>
                            <div>解锁阙值：{{detail.valve}}/0.5</div>
                        </span>
                        <span class="description" v-if="editMessage==true">
                            123
                        </span>
                    </div>
                </div>
            </div>
            <div class="session">
                <div class="navTitle">
                    <span class="title">实验详情</span>
                    <i @click="editDetail=true" class="el-icon-edit-outline"></i>
                </div>
                <div class="content">
                    {{detail.experimentText}}
                </div>
            </div>
            <div class="session">
                <div class="navTitle">
                    <span class="title">实验附件</span>
                    <i @click="editFile=true" class="el-icon-edit-outline"></i>
                </div>
                <div class="content">
                    {{detail.experimentDetailFile}}
                </div>
            </div>
            <div class="session">
                <div class="navTitle">
                    <span class="title">实验答案</span>
                    <i @click="editAnswer=true" class="el-icon-edit-outline"></i>
                </div>
                <div class="content">
                    {{detail.experimentText}}
                </div>
            </div>
            <div class="session">
                <div class="navTitle">
                    <span class="title">实验答案附件</span>
                    <i @click="editAnswerFile=true" class="el-icon-edit-outline"></i>
                </div>
                <div class="content">
                    {{detail.experimentDetailFile}}
                </div>
            </div>
            <div class="session">
                <div class="navTitle">
                    <span class="title">实验操作</span>
                    <i @click="editOperate=true" class="el-icon-edit-outline"></i>
                </div>
                <div class="content operation">
                    <el-row>
                        <span v-if="isLock==false">
                            <el-button type="warning"  @click="lockExperiment()">锁定实验</el-button>
                            <div>锁定完状态可以恢复</div>
                        </span>
                        <span v-if="isLock==true">
                            <el-button type="success" :disabled="isOver" @click="recoverExperiment()">解锁实验</el-button>
                            <div>锁定完状态可以恢复</div>
                        </span>
                        <span>
                            <el-button type="danger" @click="overExperiment()">完结实验</el-button>
                            <div>结束了就不可以恢复</div>
                        </span>
                        <span>
                            <el-button type="info" :disabled="!isOver" @click="delExperiment()">删除实验</el-button>
                            <div>只有完结课程才可以删除</div>
                        </span>
                    </el-row>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
import breadCrumb from '@/components/breadCrumb'
export default {
    props: {

    },
    data() {
        return {
            navData:[
                {
                    name: "实验列表",
                    path: "/admin/courseList"
                },{
                    name: "实验详情",
                    path: ""
                }
            ],
            isLock: false,
            detail: {

            },
            editMessage: false,
            editDetail: false,
            editFile: false,
            editAnswer: false,
            editAnswerFile: false,
            editOperate: false,
        };
    },
    computed: {

    },
    created() {
        this.$http.get(`/teaching/teacher/experiment/detail/${this.$route.params.courseId}`
        ).then((res) => { 
            if(res.data.code == "0"){
                this.detail = res.data.data
            }else if (res.data.code == "1") {
                this.$router.push('/login'); 
            }else{
                this.$message({
                    message: res.data.msg,
                    type: 'error'
                });
            }
            this.loading=false
        })
        .catch(function (error) {
            console.log(error)
        })
    },
    mounted() {

    },
    watch: {

    },
    methods: {
        lockExperiment(){
            this.$http.get(`/teaching/teacher/experiment/lock/${this.$route.params.courseId}`
            ).then((res) => { 
                if(res.data.code == "0"){
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    });
                    this.isLock=true
                }else if (res.data.code == "1") {
                    this.$router.push('/login'); 
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error'
                    });
                }
            })
            .catch(function (error) {
                console.log(error)
            })
        },
        recoverExperiment(){
            this.$http.get(`/teaching/teacher/experiment/unlock/${this.$route.params.courseId}`
            ).then((res) => { 
                if(res.data.code == "0"){
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    });
                    this.isLock=false
                }else if (res.data.code == "1") {
                    this.$router.push('/login'); 
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error'
                    });
                }
            })
            .catch(function (error) {
                console.log(error)
            })
        },
        overExperiment(){
            this.$http.get(`/teaching/teacher/experiment/end/${this.$route.params.courseId}`
            ).then((res) => { 
                if(res.data.code == "0"){
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    });
                    this.isOver=true
                }else if (res.data.code == "1") {
                    this.$router.push('/login'); 
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error'
                    });
                }
            })
            .catch(function (error) {
                console.log(error)
            })
        },
        delExperiment(){
            this.$http.get(`/teaching/teacher/experiment/invalid/${this.$route.params.courseId}`
            ).then((res) => { 
                if(res.data.code == "0"){
                    this.$message({
                        message: res.data.msg,
                        type: 'success'
                    });
                }else if (res.data.code == "1") {
                    this.$router.push('/login'); 
                }else{
                    this.$message({
                        message: res.data.msg,
                        type: 'error'
                    });
                }
            })
            .catch(function (error) {
                console.log(error)
            })
        }
    },
    components: {
        breadCrumb
    },
};
</script>

<style scoped lang="scss">
.page{
    padding: 0 1em;
    .containner{
        padding:0 1em;
        .session{
            .navTitle{
                padding:10px;
                position: relative;
                border-bottom: 1px solid #E9E9E9;
                height: 1em;
                .title{
                    float: left;
                }
                i{
                    float: right;
                }
            }
            .content{
                padding: 1em;
                .item{
                    .description{
                        display: inline-block;
                        line-height: 2em;
                        margin-left: 1em;
                    }
                }
            }
            .operation{
                span{
                    padding: 10px;
                    text-align: center;
                    display: inline-block;
                    div{
                        padding: 7px;
                        font-size: 12px;
                        color:#E02020;
                    }
                }
            }

        }
    }
 
}
</style>



